.input {
  position: relative;
  width: 320px;
  border: none;
  border-bottom: 1px solid #333333;
  outline: none;
  line-height: 1.4;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
}

input::placeholder {
  position: relative;
  left: -2px;
  top: -1px;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #828282;
}
