.button {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 5px;
  width: 113px;
  height: 38px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  cursor: url("../../../assets/images/cursor-hand.png"), default;
  outline: none;
  background-color: #fff;
}

.search__button {
  padding: 0;
  color: #E5261E;
}

.login__button {
  background-color: red;
  color: white;
  &:hover {
      background-color: #CC221B;
  }
}

.align {
  text-align: center;
  align-self: center;
}

.button_exit {
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 5px;
    width: 113px;
    height: 38px;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    cursor: url("../../../assets/images/cursor-hand.png"), default;
    outline: none;
    background-color: white;
    color: #E5261E;
}